/* Точки (булиты) */
.slick-dots {display: flex;align-items: center;justify-content: center;}

.slick-dots li {
margin: 0 9px;
list-style: none;
}

/* Активная точка */
.slick-dots li.slick-active button {
    background-color: inherit;
    border-radius: 0%;
    background-image: url(../../images/Customer-reviews/home.svg);
    background-repeat: no-repeat;
    background-size: contain;
}
.slick-dots li.slick-active button:hover{
    background-image: url(../../images/Customer-reviews/home.svg);
}

/* Элемент точки */
.slick-dots li button {
    cursor: pointer;
    border: 0;
    border-radius: 50%;
    font-size: 0;
    width: 12px;
    height: 12px;
    /* background-color: #d9d9d9; */
    background-image: url(../../images/Customer-reviews/Ellipse.svg);
    background-repeat: no-repeat;
    background-size: contain;
    
}
.slick-dots li button:hover{
    /* background-color: #D31342; */
    background-image: url(../../images/Customer-reviews/Ellipse-red.svg);
    background-repeat: no-repeat;
    background-size: contain;
}
    